import React from "react";
import { Inputtest } from "./TodoInput";
import { TaskItem } from "./TodoItem";

const Todo = () => {
  const [tasks, setTasks] = React.useState([]);

  function handleClick(text) {
    const payload = {
      id: Date.now() + text + Math.random(),
      title: text,
      status: false
    };
    setTasks([...tasks, payload]);
  }
  const handleToggle = (id) => {
    // console.log(id);
    const updatetask = tasks.map(
      (elem) => {
        if (elem.id === id) {
          elem.status = !elem.status;
        }
        return elem;
      }
      // elem.id === id ? { ...elem, status: !elem.status } : elem
    );
    setTasks(updatetask);
  };

  const handleDelete = (id) => {
    console.log(id);
    let updatetask = tasks.filter((elem) => {
      return elem.id !== id;
    });
    setTasks(updatetask);
  };
  // console.log(task);
  return (
    <div>
      <div>
        <Inputtest handleClick={handleClick} />
      </div>
      <div>
        {tasks.map((item) => {
          return (
            <TaskItem
              key={item.id}
              {...item}
              handleToggle={handleToggle}
              handleDelete={handleDelete}
            />
          );
        })}
      </div>
    </div>
  );
};

export { Todo };
