function TaskItem(props) {
  const { id, title, status, handleToggle, handleDelete } = props;
  // console.log(handleToggle);

  const styles = {
    outline: "7px solid #87fcc5"
  };
  if (status) {
    styles.outline = "7px solid #7F7BFF";
  }
  return (
    <div className="task">
      <div>{title}</div>

      <div>
        <span className="taskstatustick">{status ? "✔️" : "✖️"}</span>
        {/* <input className="radiobtn" type="radio" /> */}
        <button
          onClick={() => {
            handleToggle(id);
          }}
          style={styles}
          className="radiobtn"
        ></button>
        {/* <button className="radiobtn">Delete</button> */}
        {/* <span
          onClick={() => {
            handleDelete(id);
          }}
        >
          ❌
        </span> */}
        <img
          onClick={() => {
            handleDelete(id);
          }}
          width="30px"
          src="https://icon-library.com/images/delete-icon/delete-icon-13.jpg"
          alt=""
        />
      </div>
    </div>
  );
}

export { TaskItem };
